/*--------------------------------------------------------------
# Main Slider
--------------------------------------------------------------*/
.main-slider {
    position: relative;
    background-color: var(--tecture-black);
    z-index: 10;
}

.main-slider .item {
    background-color: var(--tecture-black);
    position: relative;
    padding-top: 311px;
    padding-bottom: 181px;
    z-index: 10;
}

.main-slider__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transition: opacity 1500ms ease-in, -webkit-transform 7000ms ease;
    transition: opacity 1500ms ease-in, -webkit-transform 7000ms ease;
    transition: transform 7000ms ease, opacity 1500ms ease-in;
    transition: transform 7000ms ease, opacity 1500ms ease-in, -webkit-transform 7000ms ease;
    z-index: 1;
}

.main-slider__bg:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(8, 10, 7, .80);
    z-index: -1;
}

.swiper-slide-active .main-slider__bg {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}

.main-slider__shape-1 {
    position: absolute;
    top: 0;
    right: 217px;
    border-top: 250px solid var(--tecture-base);
    border-right: 215px solid transparent;
    border-left: 314px solid transparent;
    opacity: 0;
    transform: translateY(-200px);
    z-index: 1;
}

.swiper-slide-active .main-slider__shape-1 {
    opacity: .30;
    transform: translateY(0px);
    transition: all 1000ms ease;
}

.main-slider__shape-2 {
    position: absolute;
    top: 0;
    right: -20px;
    border-top: 250px solid var(--tecture-base);
    border-right: 195px solid transparent;
    border-left: 215px solid transparent;
    opacity: 0;
    transform: translateY(-200px);
    z-index: 1;
}

.swiper-slide-active .main-slider__shape-2 {
    opacity: .20;
    transform: translateY(0px);
    transition: all 1000ms ease;
    transition-delay: 1500ms;
}

.main-slider__shape-3 {
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom: 415px solid var(--tecture-black);
    border-right: 525px solid transparent;
    border-left: 725px solid transparent;
    opacity: 0;
    transform: translateY(400px);
    z-index: 1;
}

.swiper-slide-active .main-slider__shape-3 {
    opacity: .50;
    transform: translateY(0px);
    transition: all 1000ms ease;
    transition-delay: 1000ms;
}

.main-slider__shape-4 {
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom: 415px solid var(--tecture-black);
    border-right: 525px solid transparent;
    border-left: 725px solid transparent;
    opacity: 0;
    transform: translateY(400px);
    z-index: 1;
}

.swiper-slide-active .main-slider__shape-4 {
    opacity: .40;
    transform: translateY(0px);
    transition: all 1000ms ease;
    transition-delay: 1500ms;
}

.main-slider__content {
    position: relative;
    display: block;
    text-align: center;
    z-index: 10;
}

.main-slider__video-link {
    position: relative;
    display: block;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-120px);
    transform: translateY(-120px);
    -webkit-transition-delay: 1000ms;
    transition-delay: 1000ms;
    -webkit-transition: opacity 2000ms ease, -webkit-transform 2000ms ease;
    transition: opacity 2000ms ease, -webkit-transform 2000ms ease;
    transition: transform 2000ms ease, opacity 2000ms ease;
    transition: transform 2000ms ease, opacity 2000ms ease, -webkit-transform 2000ms ease;
}

.main-slider__video-icon {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 95px;
    height: 95px;
    line-height: 95px;
    text-align: center;
    font-size: 25px;
    color: var(--tecture-white);
    background-color: rgba(var(--tecture-white-rgb), .30);
    border-radius: 50%;
    margin: 0 auto 0;
    transition: all 0.5s ease;
    -moz-transition: all 0.5s ease;
    -webkit-transition: all 0.5s ease;
    -ms-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
}

.main-slider__video-icon:hover {
    background-color: var(--tecture-base);
    color: var(--tecture-white);
}

.main-slider__video-link .ripple,
.main-slider__video-icon .ripple:before,
.main-slider__video-icon .ripple:after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 95px;
    height: 95px;
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -ms-box-shadow: 0 0 0 0 rgba(var(--tecture-white-rgb), 0.6);
    -o-box-shadow: 0 0 0 0 rgba(var(--tecture-white-rgb), 0.6);
    -webkit-box-shadow: 0 0 0 0 rgba(var(--tecture-white-rgb), 0.6);
    box-shadow: 0 0 0 0 rgba(var(--tecture-white-rgb), 0.6);
    -webkit-animation: ripple 3s infinite;
    animation: ripple 3s infinite;
}

.main-slider__video-icon .ripple:before {
    -webkit-animation-delay: 0.9s;
    animation-delay: 0.9s;
    content: "";
    position: absolute;
}

.main-slider__video-icon .ripple:after {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
    content: "";
    position: absolute;
}

.main-slider__title {
    position: relative;
    display: block;
    font-size: 70px;
    color: var(--tecture-white);
    font-weight: 700;
    line-height: 90px;
    text-transform: uppercase;
    margin-top: 27px;
    margin-bottom: 10px;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(-120px);
    transform: translateY(-120px);
    -webkit-transition-delay: 1100ms;
    transition-delay: 1100ms;
    -webkit-transition: opacity 2000ms ease, -webkit-transform 2000ms ease;
    transition: opacity 2000ms ease, -webkit-transform 2000ms ease;
    transition: transform 2000ms ease, opacity 2000ms ease;
    transition: transform 2000ms ease, opacity 2000ms ease, -webkit-transform 2000ms ease;
}


.main-slider__btn-box {
    position: relative;
    display: block;
    margin-top: 46px;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translateY(120px);
    transform: translateY(120px);
    -webkit-transition-delay: 1200ms;
    transition-delay: 1200ms;
    -webkit-transition: opacity 2000ms ease, -webkit-transform 2000ms ease;
    transition: opacity 2000ms ease, -webkit-transform 2000ms ease;
    transition: transform 2000ms ease, opacity 2000ms ease;
    transition: transform 2000ms ease, opacity 2000ms ease, -webkit-transform 2000ms ease;
}

.main-slider__btn {
    padding: 18px 30px 18px;
}

.main-slider__btn:after {
    width: 51% !important;
}

.swiper-slide-active .main-slider__video-link {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translateY(0) translateX(0);
    transform: translateY(0) translateX(0);
}

.swiper-slide-active .main-slider__title {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translateY(0) translateX(0);
    transform: translateY(0) translateX(0);
}

.swiper-slide-active .main-slider__btn-box {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translateY(0) translateX(0);
    transform: translateY(0) translateX(0);
}

.main-slider .owl-theme .owl-dots {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 1320px;
    width: 100%;
    position: absolute;
    bottom: 61px;
    left: 0;
    right: 0;
    padding: 0 15px;
    margin: 0 auto;
    height: 0;
    line-height: 0;
}

.main-slider .owl-theme .owl-dots .owl-dot+.owl-dot {
    margin-left: 10px;
}

.main-slider .owl-theme .owl-dots .owl-dot span {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: rgba(var(--tecture-white-rgb), .30);
    border: 2px solid transparent;
    margin: 0;
    -webkit-transition: all 300ms ease;
    transition: all 300ms ease;
}

.main-slider .owl-theme .owl-dots .owl-dot:hover span,
.main-slider .owl-theme .owl-dots .owl-dot.active span {
    background-color: rgba(var(--tecture-white-rgb), 0);
    border: 2px solid var(--tecture-white);
}



.main-slider .owl-theme .owl-nav {
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    z-index: 100;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    max-width: 100%;
    width: 100%;
    padding: 0px 80px;
    margin: 0 auto;
    height: 0;
    line-height: 0;
    transform: translateY(-50%);
}

.main-slider .owl-theme .owl-nav [class*=owl-] {
    position: relative;
    top: auto;
    left: auto;
    right: auto;
    bottom: auto;
    z-index: 100;
    width: 60px;
    height: 60px;
    font-size: 20px !important;
    color: rgba(var(--tecture-white-rgb), .30) !important;
    opacity: 1;
    margin: 0;
    text-align: center;
    transition: all 500ms ease;
    border-radius: 50%;
    background-color: transparent !important;
    border: 2px solid rgba(var(--tecture-white-rgb), .30) !important;
}

.main-slider .owl-theme .owl-nav [class*=owl-]:hover {
    color: rgba(var(--tecture-white-rgb), 1) !important;
    border: 2px solid rgba(var(--tecture-white-rgb), 1) !important;
}

.main-slider .owl-theme .owl-nav [class*=owl-]+[class*=owl-] {
    margin-left: 0px;
}

/*--------------------------------------------------------------
