}

.main-slider-two__btn-box {
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    opacity: 0;
    transform: perspective(400px) rotateY(0deg) translateY(80px);
    transform-origin: bottom;
